import React, { useState } from 'react';
import { getAuth, signInWithEmailAndPassword, sendPasswordResetEmail } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Login = ({ setIsLoggedIn }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isResettingPassword, setIsResettingPassword] = useState(false);
  const [showResetPasswordModal, setShowResetPasswordModal] = useState(false);
  const [resetEmail, setResetEmail] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const navigate = useNavigate();

  const handleLoginSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    const auth = getAuth();
    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password);
      console.log("Inicio de sesión exitoso");
      localStorage.setItem('isLoggedIn', 'true');
      setIsLoggedIn(true);
      navigate("/administrar");
    } catch (error) {
      console.log("Error al iniciar sesión:", error.message);
      setError(error.message);
    } finally {
      setIsLoading(false);
    }
  };

  const handleResetPassword = async (e) => {
    e.preventDefault();
    setIsResettingPassword(true);
    const auth = getAuth();
    try {
      await sendPasswordResetEmail(auth, resetEmail);
      console.log("Correo de restablecimiento de contraseña enviado");
      setIsResettingPassword(false);
      toast.success("Correo de restablecimiento de contraseña enviado correctamente", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      setShowResetPasswordModal(false);
    } catch (error) {
      console.log("Error al enviar correo de restablecimiento de contraseña:", error.message);
      setError(error.message);
      setIsResettingPassword(false);
      toast.error("Error al enviar correo de restablecimiento de contraseña", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  return (
    <div className="container mt-5">
      <h2>Iniciar sesión</h2>
      <form onSubmit={handleLoginSubmit}>
        <div className="mb-3">
          <label>Correo electrónico:</label>
          <input type="email" className="form-control" value={email} onChange={(e) => setEmail(e.target.value)} />
        </div>
        <div className="mb-3">
          <label>Contraseña:</label>
          <div className="input-group">
            <input type={showPassword ? "text" : "password"} className="form-control" value={password} onChange={(e) => setPassword(e.target.value)} />
            <div className="input-group-append">
              <button type="button" className="btn btn-outline-secondary" onClick={(e) => {
                e.preventDefault();
                setShowPassword(!showPassword);
              }}>
                {showPassword ? <i className="fas fa-eye-slash"></i> : <i className="fas fa-eye"></i>}
              </button>
            </div>
          </div>
        </div>
        <button type="submit" className="btn btn-primary" disabled={isLoading}>
          {isLoading ? "Cargando..." : "Iniciar sesión"}
        </button>
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <p>
         {' '}
          <a href="#" onClick={(e) => {
            e.preventDefault();
            setShowResetPasswordModal(true);
          }}>
             ¿Olvidaste tu contraseña?  
          </a>
        </p>
      </form>



      {showResetPasswordModal && (
  <div
    className="modal"
    style={{ display: 'block' }}
    tabIndex="-1"
    role="dialog"
    aria-modal="true"
    aria-hidden="false"
    aria-label="Modal de restablecimiento de contraseña"
  >
    <div className="modal-dialog" role="document">
      <div className="modal-content">
        <div className="modal-header">
          <h5 className="modal-title">Restablecer contraseña</h5>
          <button
            type="button"
            className="close"
            onClick={(e) => {
              e.preventDefault();
              setShowResetPasswordModal(false);
            }}
            aria-label="Cerrar modal"
          >
            &times;
          </button>
        </div>
        <div className="modal-body">
          <form onSubmit={handleResetPassword}>
            <div className="mb-3">
              <label>Correo electrónico:</label>
              <input
                type="email"
                className="form-control"
                value={resetEmail}
                onChange={(e) => setResetEmail(e.target.value)}
                aria-label="Correo electrónico"
              />
            </div>
            <button
              type="submit"
              className="btn btn-secondary"
              disabled={isResettingPassword}
              aria-label="Restablecer contraseña"
            >
              {isResettingPassword ? "Enviando..." : "Restablecer contraseña"}
            </button>
          </form>
        </div>
      </div>
    </div>
  </div>
)}



      <ToastContainer />
    </div>
  );
};

export default Login;