import React, { useState, useEffect } from 'react';
import { db } from '../firebase';
import { collection, onSnapshot } from 'firebase/firestore';

const Logs = () => {
  const [logs, setLogs] = useState([]);

  useEffect(() => {
    const logsRef = collection(db, 'logs');
    const unsubscribe = onSnapshot(logsRef, (snapshot) => {
      const logs = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      setLogs(logs);
    });
    return unsubscribe;
  }, []);

  return (
    <div>
      <h2>Logs</h2>
      <table className="table table-striped table-hover">
        <thead className="thead-dark">
          <tr>
            <th>Tipo</th>
            <th>Correo del usuario</th>
            <th>Producto</th>
            <th>Fecha</th>
          </tr>
        </thead>
        <tbody>
          {logs.map((log) => (
            <tr key={log.id}>
              <td>{log.tipo}</td>
              <td>{log.correo}</td>
              <td>
                {log.producto && (
                  <ul>
                    <li>Proveedor: {log.producto.proveedor}</li>
                    <li>SKU: {log.producto.sku}</li>
                    <li>Descripción: {log.producto.descripcion}</li>
                    <li>Tipo: {log.producto.tipo}</li>
                    <li>Subtipo: {log.producto.subtipo}</li>
                    <li>Costo: {log.producto.costo}</li>
                  </ul>
                )}
              </td>
              <td>{log.fecha}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Logs;