import React, { useState, useEffect } from 'react';
import { BrowserRouter, Route, Routes, Navigate } from 'react-router-dom';
import Login from './components/Login';
import AdministracionProductos from './components/AdministracionProductos';
import ConsultaCotizaciones from './components/ConsultaCotizaciones';
import Logs from './components/Logs';
import { useNavigate } from 'react-router-dom';

const BotonAdministrarProductos = ({ isLoggedIn }) => {
  const navigate = useNavigate();

  const handleAdministrarProductos = () => {
    navigate('/administrar');
  };

  const handleLogs = () => {
    navigate('/logs');
  };



  
  return (
    <div>
      {isLoggedIn && (
        <div className="btn-group">
          <button className="btn btn-success" onClick={handleAdministrarProductos}>
            Administrar productos
          </button>

          <button className="btn btn-info" onClick={handleLogs}>
            Logs
          </button>
      
     
     
        </div>
      )}
    </div>
  );
};

const App = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    const storedIsLoggedIn = localStorage.getItem('isLoggedIn');
    if (storedIsLoggedIn === 'true') {
      setIsLoggedIn(true);
    }
  }, []);

  return (
    <BrowserRouter>
      <div className="container">


      <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div className="container-fluid">
    <a className="navbar-brand" href="/consulta-cotizaciones">COTIZADOR DUPAZA</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
      
      </ul>
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        {isLoggedIn ? (
          <>
            <li className="nav-item">
              <BotonAdministrarProductos isLoggedIn={isLoggedIn} />
            </li>
            <li className="nav-item">
              <button type="button" className="btn btn-danger" onClick={() => {
                localStorage.removeItem('isLoggedIn');
                setIsLoggedIn(false);
              }}>
                Salir
              </button>
            </li>
          </>
        ) : (
          <li className="nav-item">
            <button type="button" className="btn btn-primary" onClick={() => {
              window.location.href = '/login';
            }}>
              Iniciar sesión
            </button>
          </li>
        )}
      </ul>
    </div>
  </div>
</nav>



      </div>
      <Routes>
  <Route path="/" element={<Navigate to="/consulta-cotizaciones" replace />} />
  <Route path="/consulta-cotizaciones" element={<ConsultaCotizaciones />} />
  <Route
    path="/logs"
    element={
      isLoggedIn ? (
        <Logs />
      ) : (
        <Navigate to="/login" replace />
      )
    }
  />
  <Route
    path="/administrar"
    element={
      isLoggedIn ? (
        <AdministracionProductos />
      ) : (
        <Navigate to="/login" replace />
      )
    }
  />
  <Route path="/login" element={<Login setIsLoggedIn={setIsLoggedIn} />} />
</Routes>
    </BrowserRouter>
  );
};

export default App;